import React, { useState } from 'react'
import styled from '@emotion/styled'
import { Box, Button, Flex, Text, Input } from '@chakra-ui/react'
import { subscribe } from '../../state/user/source'

const NewsletterWrapper = styled(Box)`
  position: relative;
  ${({ plain, theme }) =>
    !plain &&
    `
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  align-items: center;
  border: 1px solid;
  border-color: ${theme.colors.border};
  padding: 40px;
 
  `}
`

const Header = styled(Text)`
  z-index: 9;
  max-width: 320px;
  font-size: 20px;
  font-weight: 600;
`

const FlexWrapper1 = styled(Flex)``

const FlexWrapper2 = styled(Flex)`
  position: relative;
`

const InputWrapper = styled(Input)`
  margin-top: 24px;
  margin-right: 20px;
  z-index: 9;
`

const ButtonWrapper = styled(Button)`
  margin-top: 24px;
  height: 40px !important;
  border-radius: 6px !important;
  z-index: 9;
`

const Circle1 = styled.div`
  width: ${(p) => (p.theme.size === 'sm' ? '50px' : '200px')};
  height: ${(p) => (p.theme.size === 'sm' ? '50px' : '200px')};
  border-radius: 50%;
  background-color: #f5f5f5;
  position: absolute;
  top: -100px;
  left: -120px;
  z-index: 1;
`

const Circle2 = styled.div`
  width: ${(p) => (p.theme.size === 'sm' ? '50px' : '200px')};
  height: ${(p) => (p.theme.size === 'sm' ? '50px' : '200px')};
  border-radius: 50%;
  background-color: #f5f5f5;
  position: absolute;
  bottom: -100px;
  right: -120px;
  z-index: 1;
`

const Message = styled.div`
  margin-top: 8px;
  //position: ${({ plain }) => (plain ? 'unset' : 'absolute')};

  p {
    color: ${(p) => (p.error ? 'red' : 'green')};
    font-size: 11px;
  }
`

const Subscribe = ({ plain = false, accessKey }) => {
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState('')
  const [error, setError] = useState('')
  const [email, setEmail] = useState('')
  const onEmailChange = (e) => {
    setEmail(e.target.value)
  }
  const handleSubmit = async (e) => {
    e.preventDefault()
    e.stopPropagation()
    try {
      setError('')
      setSuccess('')
      if (!email) return setError('Please provide email!')
      setLoading(true)
      const res = await subscribe(email, accessKey)
      setSuccess('Subscription Successful! 🎉')
      setEmail('')
    } catch (e) {
      setError('Failed to subscribe')
    } finally {
      setLoading(false)
    }
  }
  return (
    <form onSubmit={handleSubmit} name="newsletter" id="subscribe">
      <NewsletterWrapper plain={plain}>
        {!plain && <Circle1 />}
        <FlexWrapper1 ml={'auto'} mr={'auto'} p={'20px'} flexDirection={['column', 'column', 'row']}>
          {!plain && <Header mr={[0, 0, '80px']}>Subscribe for weekly updates on upcoming mints</Header>}
          <FlexWrapper2 flexDirection="column">
            <InputWrapper
              placeholder="Enter your Email"
              type="email"
              name="email"
              value={email}
              onChange={onEmailChange}
            />
            <Message plain={plain} error={!!error}>
              {error ? <p>{error}</p> : <p>{success}</p>}
            </Message>
          </FlexWrapper2>
          <ButtonWrapper ml={2} type="submit" htmlType="submit" disabled={true} isLoading={loading}>
            Subscribe
          </ButtonWrapper>
        </FlexWrapper1>
        {!plain && <Circle2 />}
      </NewsletterWrapper>
    </form>
  )
}

export default Subscribe
