import axios from 'axios'
import { SDK_API } from '../../constants/endpoints'

export const subscribe = async (email, accessKey) => {
  try {
    const res = await axios.post(
      `${SDK_API}/subscription`,
      {
        email,
      },
      {
        headers: {
          'X-API-KEY': accessKey,
        },
      },
    )
    return res.data
  } catch (e) {
    throw new Error(e)
  }
}
